@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.no-select{-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}[data-tooltip-location="left"]:hover:after{-webkit-transform:translateY(50%) scaleX(1);transform:translateY(50%) scaleX(1)}[data-tooltip-location="right"]:before,[data-tooltip-location="right"]:after{left:calc(100% + 5px);bottom:50%;margin-bottom:3vh}[data-tooltip-location="right"]:before{-webkit-transform:translate(5px, 50%) scale(0.5);transform:translate(5px, 50%) scale(0.5);margin-left:3vw}[data-tooltip-location="right"]:hover:before{-webkit-transform:translate(5px, 50%) scale(1);transform:translate(5px, 50%) scale(1)}[data-tooltip-location="right"]:after{border-width:5px 5px 5px 0px;border-color:transparent rgba(55,64,70,0.9) transparent transparent;-webkit-transform-origin:right;transform-origin:right;-webkit-transform:translateY(50%) scaleX(0);transform:translateY(50%) scaleX(0)}[data-tooltip-location="right"]:hover:after{-webkit-transform:translateY(50%) scaleX(1);transform:translateY(50%) scaleX(1)}[data-tooltip-location="bottom"]:before,[data-tooltip-location="bottom"]:after{top:calc(100% + 5px);bottom:auto}[data-tooltip-location="bottom"]:before{-webkit-transform:translate(-50%, 5px) scale(0.5);transform:translate(-50%, 5px) scale(0.5)}[data-tooltip-location="bottom"]:hover:before{-webkit-transform:translate(-50%, 5px) scale(1);transform:translate(-50%, 5px) scale(1)}[data-tooltip-location="bottom"]:after{border-width:0px 5px 5px 5px;border-color:transparent transparent rgba(55,64,70,0.9) transparent;-webkit-transform-origin:bottom;transform-origin:bottom}[data-tooltip]{position:relative;z-index:0;font-weight:lighter}[data-tooltip]:before,[data-tooltip]:after{position:absolute;visibility:hidden;opacity:0;left:50%;bottom:calc(100% + 5px);pointer-events:none;-webkit-transition:0.1s;transition:0.1s;will-change:transform}[data-tooltip]:before{content:attr(data-tooltip);padding:10px 18px;min-width:50px;max-width:300px;width:-webkit-max-content;width:max-content;width:-moz-max-content;border-radius:6px;font-size:14px;background-color:rgba(59,72,80,0.9);background-image:linear-gradient(30deg, rgba(59,72,80,0.44), rgba(59,68,75,0.44), rgba(60,82,88,0.44));box-shadow:0px 0px 24px rgba(0,0,0,0.2);color:#fff;text-align:center;white-space:pre-wrap;-webkit-transform:translate(-50%, -5px) scale(0.5);transform:translate(-50%, -5px) scale(0.5)}[data-tooltip]:after{content:'';border-style:solid;border-width:5px 5px 0px 5px;border-color:rgba(55,64,70,0.9) transparent transparent transparent;-webkit-transition-duration:0s;transition-duration:0s;-webkit-transform-origin:top;transform-origin:top;-webkit-transform:translateX(-50%) scaleY(0);transform:translateX(-50%) scaleY(0)}[data-tooltip]:hover:before,[data-tooltip]:hover:after{visibility:visible;opacity:1}[data-tooltip]:hover:before{-webkit-transition-delay:0.3s;transition-delay:0.3s;-webkit-transform:translate(-50%, -5px) scale(1);transform:translate(-50%, -5px) scale(1)}[data-tooltip]:hover:after{-webkit-transition-delay:0.5s;transition-delay:0.5s;-webkit-transition-duration:0.05s;transition-duration:0.05s;-webkit-transform:translateX(-50%) scaleY(1);transform:translateX(-50%) scaleY(1)}[data-tooltip-location="left"]:before,[data-tooltip-location="left"]:after{left:auto;right:calc(100% + 5px);bottom:50%}[data-tooltip-location="left"]:before{-webkit-transform:translate(-5px, 50%) scale(0.5);transform:translate(-5px, 50%) scale(0.5)}[data-tooltip-location="left"]:hover:before{-webkit-transform:translate(-5px, 50%) scale(1);transform:translate(-5px, 50%) scale(1)}[data-tooltip-location="left"]:after{border-width:5px 0px 5px 5px;border-color:transparent transparent transparent rgba(55,64,70,0.9);-webkit-transform-origin:left;transform-origin:left;-webkit-transform:translateY(50%) scaleX(0);transform:translateY(50%) scaleX(0)}

.App{text-align:center}html{background:#00416A;background:-webkit-gradient(linear, left top, right top, from(#E4E5E6), to(#00416A));background:linear-gradient(to right, #E4E5E6, #00416A)}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}


.card{font-family:'Montserrat', sans-serif;font-weight:bold;background:whitesmoke;border-radius:10px;width:50%;height:5%;padding:1%;margin:2%;box-shadow:0 7px 34px -15px rgba(0,0,0,0.75);-webkit-transition-delay:0.2s;transition-delay:0.2s;-webkit-transition:0.2s ease-out;transition:0.2s ease-out;animation:fadein 1s;-moz-animation:fadein 1s;-webkit-animation:fadein 1s;-o-animation:fadein 1s}.card:hover{box-shadow:0px 0px 21px -7px rgba(0,0,0,0.75)}.card .inline{display:inline-block}.card .vertical-center{-webkit-transform:translateY(-50%);transform:translateY(-50%)}.card.pointer{cursor:pointer}.card.pointer:hover{box-shadow:0px 10px 30px -4px rgba(0,0,0,0.5)}.card .title{padding:3% 0 0 0}.card img{max-width:31%;border-radius:50px}@-webkit-keyframes fadein{from{opacity:0;-webkit-transform:translate(0, -50px);transform:translate(0, -50px)}to{opacity:1;-webkit-transform:translate(0px, 0px);transform:translate(0px, 0px)}}@keyframes fadein{from{opacity:0;-webkit-transform:translate(0, -50px);transform:translate(0, -50px)}to{opacity:1;-webkit-transform:translate(0px, 0px);transform:translate(0px, 0px)}}@-webkit-keyframes fadein-side{from{opacity:0;-webkit-transform:translate(-50px, -50px);transform:translate(-50px, -50px)}to{opacity:1;-webkit-transform:translate(0px, 0px);transform:translate(0px, 0px)}}@keyframes fadein-side{from{opacity:0;-webkit-transform:translate(-50px, -50px);transform:translate(-50px, -50px)}to{opacity:1;-webkit-transform:translate(0px, 0px);transform:translate(0px, 0px)}}.child{margin-left:5%}.child.node .card{animation:fadein-side .5s;-moz-animation:fadein-side .5s;-webkit-animation:fadein-side .5s;-o-animation:fadein-side .5s}.hidden{display:none}.expander{-webkit-transition:0.15s ease-out;transition:0.15s ease-out;width:3%}.expander.vertical-center{-webkit-transform:translate(0%, -50%);transform:translate(0%, -50%)}.expander.rot-90-cw{-webkit-transform-origin:50% 50%;transform-origin:50% 50%;-webkit-transform:rotate(90deg);transform:rotate(90deg)}.expander.rot-90-cw.vertical-center{-webkit-transform:translate(0%, -50%) rotate(90deg);transform:translate(0%, -50%) rotate(90deg)}

